<template>
<div id="page-top">
  <div id="wrapper">
    <ul class="navbar-nav bg-gradient-primary sidebar sidebar-dark accordion" id="accordionSidebar">
      <a class="sidebar-brand d-flex align-items-center justify-content-center" href="index.html">
        <div class="sidebar-brand-icon rotate-n-15">
          <i class="fas fa-laugh-wink"></i>
        </div>
        <div class="sidebar-brand-text mx-3">EBOOK</div>
      </a>

      <hr class="sidebar-divider my-0">

      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/login'}">
          <i class="fas fa-fw fa-tachometer-alt"></i>
          <span>Log In</span>
        </router-link>
      </li>

      <hr class="sidebar-divider d-none d-md-block">

      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Book Store</span>
        </router-link>
      </li>

      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/cart' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Shop Cart</span>
        </router-link>
      </li>

      <hr class="sidebar-divider d-none d-md-block">

      <li class="nav-item">
        <router-link class="nav-link" :to="{ path: '/login' }">
          <i class="fas fa-fw fa-chart-area"></i>
          <span>Log out</span>
        </router-link>
      </li>

    </ul>

    <div id="content-wrapper" class="d-flex flex-column">
      <div id="content">
        <nav class="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">
          <button id="sidebarToggleTop" class="btn btn-link d-md-none rounded-circle mr-3">
            <i class="fa fa-bars"></i>
          </button>
        </nav>
        <div class="container-fluid">
          <div class="card shadow mb-4">
            <div class="card-header py-3">
              <div id="content-wrapper" class="d-flex flex-column">
                <p><img src="../../assets/images/logo.png"/></p>
                <div class="col-xs-12 col-md-7" id="row">
                  <h1>ICS{{item.name}}</h1>
                  <p>
                    <strong/>{{item.writer}}
                  </p>
                  <p>
                    <strong/>{{item.price}}
                  </p>
                  <p>
                    <strong/>{{item.inventory}}
                  </p>
                  <p>
                    <strong/>{{item.summary}}
                  </p>
                  <!-- <div class="input-group">
                    <span class="input-group-addon">:</span>
                    <input class="form-control" readonly :value="item.stock">
                  </div> -->
                  <a tabindex="0" id="addcart" class="btn btn-primary btn-user" role="button" @click="buy" data-trigger="focus"
                    data-placement="bottom" data-content="" v-on:click="handleAddClick()">
                    <span class="glyphicon glyphicon-shopping-cart"></span>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <footer class="sticky-footer bg-white">
        <div class="container my-auto">
          <div class="copyright text-center my-auto">
            <span>Copyright &copy; EBOOK Store 2019</span>
          </div>
        </div>
      </footer>

    </div>
  </div>
</div>
</template>

<script>
import http from '../../http-common.js'

export default {
  name: 'detail',
  data () {
    return {
      useremail: '174783@qq.com',
      item: ''
    }
  },
  mounted () {
    this.$http.get('/getitem', { params: { id: this.$route.params.id } })
      .then((response) => {
        this.item = response.data
      })
  },
  methods: {
    handleAddClick () {
      // var email = this.useremail
      var data = {
        useremail: this.useremail,
        bookname: this.item.name,
        amount: 1,
        paied: false
      }
      http
        .post('/addcart', data)
        .then(response => {
          console.log(this.selectedRows)
        })
        .catch(e => {
          console.log(e)
        })
    }
  }
}
</script>

<style scoped>
.page-top {
  background-color: white;
}
h1 {
  font-size: 300%;
  margin-bottom: 10%;
}
.form-control {
  width: 50px;
}
.btn {
  margin-top: 4%;
  font-size: 125%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>



